<template>
    <div class="page-index">
        <van-nav-bar            
            left-arrow
            title="修改签名"
            @click-left="onClickLeft"
            @click-right="onClickRight"
            right-text="保存"
        >
          
        </van-nav-bar>
        <section class="main">
            <p class="label">个性签名</p>
            <van-cell-group :border="false">
                <van-field class="bio" type="textarea" maxlength="40" v-model.trim="bio" placeholder="填写个性签名更容易获得关注"  />
            </van-cell-group>
            <van-divider />
            <p class="count">{{bio.length}}/40</p>
        </section>

    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            bio:''
        }   
    },
    created(){
        this.getUserInfo();
    },
    methods:{
        
        getUserInfo(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            }); 
            this.api.getUserInfo({
                token:this.$store.state.userData,
                uid:''
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.bio = res.data.bio;
                   
                }
            })
        },
        saveMyInfoByBio(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.saveMyInfoByBio({
                token:this.$store.state.userData,
                bio:this.bio
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                   // this.$router.go(-1)
                    Toast('修改成功');
                    setTimeout(() => {
                        let json ={method:'e_loadPage()'}
                        console.log(`teldapp://closeWebview?params=`+JSON.stringify(json));
                        window.location.href= `teldapp://closeWebview?params=`+JSON.stringify(json)
                    },900)
                }else {
                    return Toast(res.err_msg);
                }
            })
        },
        onClickLeft(){
            this.$router.go(-1)
        },
        onClickRight(){
            if(!this.bio.length){
               return Toast('签名不能为空');
            }
            this.saveMyInfoByBio();
        }
    }
}
</script>

<style lang="less" scoped>
.page-index{
    .main{
        padding: 38px 15px 0px;

        .label{
            font-size: 13px;
            
            color: #4E5578;
            margin-bottom: 30px;
        }
        .bio{
            font-size: 18px;
            color: #D1D1D1;
            margin: 0 -15px;
            margin-bottom: 45px;
        }
        .count{
            color: #D1D1D1;
            font-size: 13px;
        }
    }
}
</style>